import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  FormControlLabel,
  FormGroup,
  makeStyles,
  useMediaQuery,
  useTheme,
  Typography,
} from '@material-ui/core'

import CourseSelector from './CourseSelector'
import Filedrop from '../Filedrop'
import ListErrors from '../ListErrors'
import Spinner from '../Spinner'
import agent from '../../agent'

import {
  IMPORT_SCORES,
  IMPORT_SCORES_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  dialog: {
    '& > .MuiDialog-container > .MuiPaper-root': {
      [theme.breakpoints.up('lg')]: {
        width: theme.spacing(100),
      },
    },
  },
}))

const mapStateToProps = (state) => ({
  ...state.scores,
})

const mapDispatchToProps = (dispatch) => ({
  onImport: (data, curStudent) =>
    dispatch({
      type: IMPORT_SCORES,
      payload: agent.Scores.import(data),
      curStudent,
      snackbar: { message: 'Scores imported', variant: 'success' },
    }),
  onExit: () => dispatch({ type: IMPORT_SCORES_DIALOG_UNLOADED }),
})

const ScoreImportStepper = ({ onDataChange, onShowImport }) => {
  const [file, setFile] = useState(null)
  const [course, setCourse] = useState(null)
  const [tasks, setTasks] = useState(null)

  const handleCourseSelect = (course) => {
    setCourse(course)
  }

  const handleFiledrop = (file) => {
    const tasks = {}
    Object.keys(file[0])
      .slice(1)
      .forEach((task) => {
        tasks[task] = false
      })
    setFile(file)
    setTasks(tasks)
  }

  const handleTaskChange = (ev) => {
    setTasks({ ...tasks, [ev.target.name]: ev.target.checked })
  }

  // Determine if any tasks have been checked/selected
  useEffect(() => {
    const tasksSelected = tasks
      ? Object.values(tasks).some((el) => el === true)
      : false
    onShowImport(tasksSelected)
  }, [tasks])

  // Keep the imported data object current
  useEffect(() => {
    onDataChange({ file, course, tasks })
  }, [file, course, tasks])

  return (
    <>
      {!file && !course && !tasks && (
        <CourseSelector onSelect={handleCourseSelect} />
      )}

      {!file && course && !tasks && (
        <div>
          <Typography gutterBottom>
            Scores must be imported from a .csv file exported from Infinite
            Campus
          </Typography>
          <Filedrop onFiledrop={handleFiledrop} />
        </div>
      )}

      {file &&
      course &&
      tasks && ( // @todo: use material-table here?
          <FormControl component='fieldset'>
            <Typography gutterBottom variant='body1'>
              Select the assignments to import
            </Typography>
            <FormGroup>
              {Object.keys(tasks).map((task) => (
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={tasks[task]}
                      onChange={handleTaskChange}
                      name={task}
                    />
                  }
                  key={task}
                  label={task}
                />
              ))}
            </FormGroup>
          </FormControl>
        )}
    </>
  )
}

const ImportScoresDialog = ({
  curStudent,
  errors,
  inProgress,
  isClosed,
  isOpen,
  onExit,
  onImport,
}) => {
  const [open, setOpen] = useState(false)
  const [ready, setReady] = useState(false)
  const [data, setData] = useState(null)

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))
  const classes = useStyles()

  const handleClose = () => {
    setOpen(false)
    isClosed()
  }

  const handleDataChange = (importData) => {
    setData(importData)
  }

  const handleShowImport = (selected) => {
    setReady(selected)
  }

  const handleClickImport = () => {
    // Get student usernames (student numbers)
    const usernames = data.file
      .map(
        (student, index) =>
          index > 1 && student['Assignment Name'].split('#').pop()
      )
      .filter((username) => username !== false)

    // Prepare data to send to server
    const parsed = { course: data.course, tasks: [] }
    for (const taskName in data.tasks) {
      const selected = data.tasks[taskName]
      if (selected) {
        const task = {
          title: taskName,
          totalPoints: data.file[1][taskName],
          scores: {},
        }

        for (const username of usernames) {
          const student = data.file.find((row) =>
            row['Assignment Name'].endsWith(username)
          )
          if (student) task.scores[username] = student[taskName]
        }

        parsed.tasks.push(task)
      }
    }

    onImport(parsed, curStudent)
  }

  useEffect(() => {
    // Close the dialog only if there are no errors
    if (!inProgress && !errors) handleClose()
  }, [inProgress])

  // Allow for external component control of displaying the dialog
  useEffect(() => {
    setOpen(isOpen)
  }, [isOpen])

  return (
    <Dialog
      className={classes.dialog}
      fullScreen={fullScreen}
      onClick={(ev) => ev.stopPropagation()}
      onClose={handleClose}
      onExit={onExit}
      onFocus={(ev) => ev.stopPropagation()}
      open={open}
    >
      <DialogTitle>Import Scores</DialogTitle>
      <DialogContent>
        <ListErrors errors={errors} />
        {inProgress ? (
          <Spinner message='Importing...' />
        ) : (
          <ScoreImportStepper
            onDataChange={handleDataChange}
            onShowImport={handleShowImport}
          />
        )}
      </DialogContent>
      <DialogActions>
        {ready && (
          <Button
            color='primary'
            disabled={inProgress}
            onClick={handleClickImport}
            variant='contained'
          >
            {inProgress ? 'Importing...' : 'Import'}
          </Button>
        )}
        <Button disabled={inProgress} onClick={handleClose} variant='contained'>
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(ImportScoresDialog)
