import {
  ADD_COURSE,
  ADD_PRODUCT,
  ADD_STUDENT,
  ADD_TEACHER,
  ADD_TRANSACTION,
  ADD_TRANSACTIONS,
  DELETE_COURSE,
  DELETE_PRODUCT,
  DELETE_SONG_REQUEST,
  DELETE_STUDENT,
  DELETE_TASK,
  DELETE_TEACHER,
  DELETE_TRANSACTION,
  IMPORT_SCORES,
  IMPORT_STUDENTS,
  MOVE_STUDENT,
  RESET_STUDENT_PASSWORD,
  SETTINGS_SAVED,
  SNACKBAR_CLOSED,
  SUBMIT_SONG_REQUEST,
  SUBMIT_SONG_REMOVAL_REQUEST,
  UPDATE_COURSE,
  UPDATE_PRODUCT,
  UPDATE_STUDENT,
  UPDATE_TASK,
  UPDATE_TEACHER,
  UPDATE_TRANSACTION,
  WHATS_NEW_FLAGS_RESET,
} from '../constants/actionTypes'

export default (state = {}, action) => {
  switch (action.type) {
    case ADD_COURSE:
    case ADD_PRODUCT:
    case ADD_STUDENT:
    case ADD_TEACHER:
    case ADD_TRANSACTION:
    case ADD_TRANSACTIONS:
    case DELETE_COURSE:
    case DELETE_PRODUCT:
    case DELETE_SONG_REQUEST:
    case DELETE_STUDENT:
    case DELETE_TASK:
    case DELETE_TEACHER:
    case DELETE_TRANSACTION:
    case IMPORT_SCORES:
    case IMPORT_STUDENTS:
    case MOVE_STUDENT:
    case RESET_STUDENT_PASSWORD:
    case SETTINGS_SAVED:
    case SUBMIT_SONG_REQUEST:
    case SUBMIT_SONG_REMOVAL_REQUEST:
    case UPDATE_COURSE:
    case UPDATE_PRODUCT:
    case UPDATE_STUDENT:
    case UPDATE_TASK:
    case UPDATE_TEACHER:
    case UPDATE_TRANSACTION:
    case WHATS_NEW_FLAGS_RESET:
      return {
        ...state,
        snackbar: action.error
          ? null //{ message: 'An error occurred', variant: 'error' }
          : action.snackbar,
      }
    case SNACKBAR_CLOSED:
      return { ...state, snackbar: null }
    default:
      return state
  }
}
