import React from 'react'
import {
  GridList,
  GridListTile,
  GridListTileBar,
  Tooltip,
  Typography,
  makeStyles,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import CurrencyFormat from 'react-currency-format'

import DeleteProductDialog from './DeleteProductDialog'
import ProductDialog from './ProductDialog'

const useStyles = makeStyles((theme) => ({
  actionIcon: { display: 'flex' },
  cost: {
    backgroundColor: theme.palette.primary.main,
    color: 'white',
    fontSize: '1rem',
    lineHeight: '1.5rem',
    padding: theme.spacing(1),
  },
  description: {
    marginBottom: '-1.5rem',
    opacity: 0,
    transition: 'opacity 0s ease-in-out',
  },
  name: {
    color: theme.palette.secondary.main,
    fontSize: theme.typography.h5.fontSize,
    whiteSpace: 'nowrap',
  },
  tile: {
    '&:hover .MuiGridListTileBar-root': {
      'height': '100%',
      '& #description': {
        marginBottom: 0,
        opacity: 1,
        transition: 'margin-bottom 0.35s ease-in-out, opacity 0.5s ease-in-out',
      },
    },
  },
  tileBar: {
    alignItems: 'flex-end',
    paddingBottom: theme.spacing(1),
    transition: 'height 0.35s ease-in-out',
  },
  title: {
    whiteSpace: 'normal',
  },
}))

const ProductsGallery = ({ products, userRole }) => {
  const classes = useStyles()

  const theme = useTheme()
  const isSmall = useMediaQuery(theme.breakpoints.down('sm'))
  const isMedium = useMediaQuery(theme.breakpoints.down('md'))
  const cols = isSmall ? 1 : isMedium ? 2 : 3

  return (
    <div>
      <GridList cols={cols} spacing={8}>
        {products?.length > 0 ? (
          products.map((product) => {
            return (
              <GridListTile key={product.slug} className={classes.tile}>
                <img src={product.photoUrl} alt={product.name} />

                <GridListTileBar
                  classes={{
                    actionIcon: classes.actionIcon,
                    title: classes.title,
                  }}
                  className={classes.tileBar}
                  title={
                    <div>
                      <div className={classes.description} id='description'>
                        {product.description}
                      </div>
                      <Tooltip
                        arrow
                        enterTouchDelay={0}
                        placement='bottom-start'
                        title={product.name}
                      >
                        <div className={classes.name}>{product.name}</div>
                      </Tooltip>
                    </div>
                  }
                  subtitle={
                    <span className={classes.cost}>
                      <CurrencyFormat
                        value={product.cost}
                        displayType={'text'}
                        thousandSeparator={true}
                        suffix={' pts'}
                      />
                    </span>
                  }
                  actionIcon={
                    userRole === 'teacher' && (
                      <>
                        <ProductDialog product={product} />
                        <DeleteProductDialog product={product} />
                      </>
                    )
                  }
                />
              </GridListTile>
            )
          })
        ) : (
          <Typography>You have no products</Typography>
        )}
      </GridList>
    </div>
  )
}

export default ProductsGallery
