import React, { useEffect, useState } from 'react'
import MaterialTable from 'material-table'

const StudentsTable = ({ course, onSelect }) => {
  const [data, setData] = useState([])

  const mappedData = course.students.map((student) => ({
    username: student.username,
    firstName: student.firstName,
    lastName: student.lastName,
    fullName: `${student.firstName} ${student.lastName}`,
    role: student.role,
    _id: student._id,
  }))

  const [columns] = useState([{ title: 'Students', field: 'fullName' }])

  useEffect(() => {
    setData(mappedData)
  }, [course])

  return (
    <MaterialTable
      columns={columns}
      data={data}
      localization={{
        body: {
          emptyDataSourceMessage: 'No students found for this course',
        },
        toolbar: {
          nRowsSelected: '{0} student(s) selected',
        },
      }}
      onSelectionChange={(rows) => onSelect(rows)}
      options={{ paging: false, selection: true, toolbar: true }}
      title={`${course.name} | Period ${course.period}`}
    />
  )
}

export default StudentsTable
