import {
  ACCOUNT_VIEW_LOADED,
  ACCOUNT_VIEW_UNLOADED,
  ADD_PRODUCT,
  ASYNC_START,
  PRODUCT_DIALOG_UNLOADED,
  PRODUCTS_PAGE_LOADED,
  PRODUCTS_PAGE_UNLOADED,
  DELETE_PRODUCT,
  UPDATE_PRODUCT,
} from '../constants/actionTypes'

const defaultState = {
  products: [],
  errors: null,
  inProgress: false,
  pageLoaded: false,
}

const sortByCost = (a, b) => a.cost - b.cost

export default (state = defaultState, action) => {
  switch (action.type) {
    case ACCOUNT_VIEW_LOADED:
      return {
        ...state,
        products: action.payload[1].products,
      }
    case ACCOUNT_VIEW_UNLOADED:
    case PRODUCTS_PAGE_UNLOADED:
      return defaultState
    case ADD_PRODUCT:
      return {
        ...state,
        products: action.error
          ? state.products
          : [...state.products, action.payload.product].sort((a, b) =>
              sortByCost(a, b)
            ),
        errors: action.error ? action.payload.errors : null,
        inProgress: false,
      }
    case ASYNC_START:
      if (action.subtype === ADD_PRODUCT || action.subtype === UPDATE_PRODUCT) {
        return { ...state, errors: null, inProgress: true }
      } else if (action.subtype === PRODUCTS_PAGE_LOADED) {
        return { ...state, errors: null, pageLoaded: true }
      }
      break
    case DELETE_PRODUCT:
      return {
        ...state,
        products: action.error
          ? state.products
          : state.products.filter(
              (product) => product.slug !== action.payload.product.slug
            ),
      }
    case PRODUCT_DIALOG_UNLOADED:
      return { ...state, errors: null }
    case PRODUCTS_PAGE_LOADED:
      return {
        ...state,
        products: action.payload.products,
        pageLoaded: false,
      }
    case UPDATE_PRODUCT:
      return {
        ...state,
        products: action.error
          ? state.products
          : state.products
              .map((product) =>
                product._id === action.payload.product._id
                  ? action.payload.product
                  : product
              )
              .sort((a, b) => sortByCost(a, b)),
        errors: action.error ? action.payload.errors : null,
        inProgress: false,
      }
    default:
      return state
  }

  return state
}
