import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import MaterialTable from 'material-table'

import agent from '../../agent'

import {
  DELETE_TASK,
  TASKS_DIALOG_LOADED,
  UPDATE_TASK,
} from '../../constants/actionTypes'

const mapStateToProps = (state) => ({
  tasks: state.tasks.tasks,
})

const mapDispatchToProps = (dispatch) => ({
  onDelete: (task) =>
    dispatch({
      type: DELETE_TASK,
      payload: agent.Tasks.delete(task),
      snackbar: { message: 'Assignment removed', variant: 'success' },
    }),
  onLoad: (courseId) =>
    dispatch({
      type: TASKS_DIALOG_LOADED,
      payload: agent.Tasks.getAll(courseId),
    }),
  onUpdate: (task) =>
    dispatch({
      type: UPDATE_TASK,
      payload: agent.Tasks.update(task),
      snackbar: { message: 'Assignment updated', variant: 'success' },
    }),
})

const TasksTable = ({ course, onDelete, onLoad, onUpdate, tasks }) => {
  const [data, setData] = useState([])

  const mappedData = tasks.map((task) => ({
    createdAt: new Date(task.createdAt).toDateString(),
    title: task.title,
    totalPoints: task.totalPoints,
    slug: task.slug,
  }))

  const [columns] = useState([
    { title: 'Date', field: 'createdAt', editable: 'never' },
    { title: 'Title', field: 'title', editable: 'never' },
    { title: 'Total Points', field: 'totalPoints' },
  ])

  useEffect(() => {
    onLoad(course._id)
  }, [])

  useEffect(() => {
    setData(mappedData)
  }, [tasks])

  return (
    <MaterialTable
      columns={columns}
      data={data}
      localization={{
        body: {
          emptyDataSourceMessage: 'No assignments found for this course',
          editRow: {
            deleteText:
              'Delete assignment? This will delete all scores for this assignment.',
          },
        },
      }}
      options={{ actionsColumnIndex: -1, paging: false, toolbar: false }}
      editable={{
        onRowUpdate: (newData, oldData) =>
          new Promise((resolve, reject) => {
            const dataUpdate = [...data]
            const index = oldData.tableData.id
            dataUpdate[index] = newData
            setData([...dataUpdate])
            onUpdate(newData)
            resolve()
          }),
        onRowDelete: (oldData) =>
          new Promise((resolve, reject) => {
            const newData = [...data]
            const index = oldData.tableData.id
            newData.splice(index, 1)
            setData([...newData])
            onDelete(oldData)
            resolve()
          }),
      }}
    />
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(TasksTable)
