import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { Formik, Form, Field } from 'formik'
import { TextField } from 'formik-material-ui'
import * as Yup from 'yup'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  makeStyles,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'

import CourseSelector from './CourseSelector'
import ListErrors from '../ListErrors'
import StudentsTable from './StudentsTable'
import agent from '../../agent'

import {
  ADD_TRANSACTIONS,
  TRANSACTIONS_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  dialog: {
    '& > .MuiDialog-container > .MuiPaper-root': {
      [theme.breakpoints.up('lg')]: {
        width: theme.spacing(80),
      },
    },
  },
}))

const mapStateToProps = (state) => ({
  ...state.scores,
})

const mapDispatchToProps = (dispatch) => ({
  onAdd: (transactions, curStudent) =>
    dispatch({
      type: ADD_TRANSACTIONS,
      payload: agent.Transactions.addMany(transactions),
      curStudent,
      snackbar: {
        message: `${transactions.typeTitle} added`,
        variant: 'success',
      },
    }),
  onExit: () => dispatch({ type: TRANSACTIONS_DIALOG_UNLOADED }),
})

const BulkTransactionsDialog = ({
  curStudent,
  errors,
  inProgress,
  isClosed,
  isOpen,
  onAdd,
  onExit,
  type,
}) => {
  const [open, setOpen] = useState(false)
  const [course, setCourse] = useState(null)
  const [students, setStudents] = useState([])

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))
  const classes = useStyles()

  const handleClose = () => {
    setOpen(false)
    setCourse(null)
    isClosed()
  }

  const handleSubmitAdd = (values) => {
    onAdd(
      {
        ...values,
        students: students.map((student) => student._id),
        type: type === 'Bonuses' ? 'bonus' : 'purchase',
        typeTitle: type,
      },
      curStudent
    )
  }

  const handleCourseSelect = (slug) => {
    setCourse(slug)
  }

  const handleStudentSelect = (rows) => {
    setStudents(rows)
  }

  useEffect(() => {
    // Close the dialog only if there are no errors
    if (!inProgress && !errors) handleClose()
  }, [inProgress])

  // Allow for external component control of displaying the dialog
  useEffect(() => {
    setOpen(isOpen)
  }, [isOpen])

  return (
    <Dialog
      className={classes.dialog}
      fullScreen={fullScreen}
      onClick={(ev) => ev.stopPropagation()}
      onClose={handleClose}
      onExit={onExit}
      onFocus={(ev) => ev.stopPropagation()}
      open={open}
    >
      <Formik
        initialValues={{
          amount: '',
          description: '',
        }}
        validationSchema={Yup.object({
          amount: Yup.number().typeError('Numbers only').required('Required'),
          description: Yup.string().required('Required'),
        })}
        onSubmit={async (values) => {
          handleSubmitAdd(values)
        }}
      >
        <Form>
          <DialogTitle>Add {type}</DialogTitle>
          <DialogContent>
            <ListErrors errors={errors} />
            {!course && <CourseSelector onSelect={handleCourseSelect} />}
            {course && (
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <Field
                    autoFocus
                    color='primary'
                    component={TextField}
                    disabled={inProgress}
                    label='Description'
                    name='description'
                    type='text'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Field
                    color='primary'
                    component={TextField}
                    disabled={inProgress}
                    label='Amount'
                    name='amount'
                    type='text'
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12}>
                  <StudentsTable
                    course={course}
                    onSelect={handleStudentSelect}
                  />
                </Grid>
              </Grid>
            )}
          </DialogContent>
          <DialogActions>
            <Button
              disabled={inProgress || students.length === 0}
              color='primary'
              type='submit'
              variant='contained'
            >
              {inProgress ? 'Adding...' : 'Add'}
            </Button>
            <Button
              disabled={inProgress}
              onClick={handleClose}
              variant='contained'
            >
              Cancel
            </Button>
          </DialogActions>
        </Form>
      </Formik>
    </Dialog>
  )
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BulkTransactionsDialog)
