import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import PageHeader from '../PageHeader'
import ProductDialog from './ProductDialog'
import ProductsGallery from './ProductsGallery'
import Spinner from '../Spinner'
import agent from '../../agent'

import {
  PRODUCTS_PAGE_LOADED,
  PRODUCTS_PAGE_UNLOADED,
} from '../../constants/actionTypes'

const mapStateToProps = (state) => ({
  ...state.products,
  currentUser: state.common.currentUser,
})

const mapDispatchToProps = (dispatch) => ({
  onLoad: () =>
    dispatch({ type: PRODUCTS_PAGE_LOADED, payload: agent.Products.getAll() }),
  onUnload: () => dispatch({ type: PRODUCTS_PAGE_UNLOADED }),
})

const Products = ({ currentUser, products, onLoad, onUnload, pageLoaded }) => {
  useEffect(() => {
    onLoad()

    return () => {
      onUnload()
    }
  }, [])

  return (
    <>
      <PageHeader
        title={
          currentUser.role === 'teacher' ? 'Manage Products' : 'View Products'
        }
      >
        {currentUser.role === 'teacher' && <ProductDialog />}
      </PageHeader>
      {pageLoaded ? (
        <Spinner message='Loading...' />
      ) : (
        <ProductsGallery products={products} userRole={currentUser.role} />
      )}
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(Products)
