import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  Typography,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'

import agent from '../agent'

import { WHATS_NEW_DIALOG_UNLOADED } from '../constants/actionTypes'

const mapStateToProps = (state) => ({
  ...state.common,
})

const mapDispatchToProps = (dispatch) => ({
  onExit: () =>
    dispatch({
      type: WHATS_NEW_DIALOG_UNLOADED,
      payload: agent.Auth.save({ viewedChanges: true }), // @note: set to 'false' when testing
    }),
})

const WhatsNewDialog = ({ currentUser, onExit }) => {
  const [open, setOpen] = useState(false)

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))

  const handleClose = () => {
    setOpen(false)
  }

  useEffect(() => {
    if (currentUser)
      setOpen(currentUser.role !== 'admin' && !currentUser.viewedChanges)
  }, [currentUser])

  return (
    <Dialog
      fullScreen={fullScreen}
      fullWidth={true}
      maxWidth={'md'}
      open={open}
      onClose={handleClose}
      onClick={(ev) => ev.stopPropagation()}
      onExit={onExit}
      onFocus={(ev) => ev.stopPropagation()}
    >
      <DialogTitle>What's New in Chem Cash!</DialogTitle>
      <DialogContent>
        {currentUser?.role === 'student'
          ? StudentWhatsNewContent()
          : TeacherWhatsNewContent()}
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} variant='contained'>
          Close
        </Button>
      </DialogActions>
    </Dialog>
  )
}

