import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  useMediaQuery,
  useTheme,
  Typography,
} from '@material-ui/core'
import { Publish } from '@material-ui/icons'

import ActionButton from '../ActionButton'
import Filedrop from '../Filedrop'
import Spinner from '../Spinner'
import agent from '../../agent'

import {
  IMPORT_STUDENTS,
  STUDENT_DIALOG_UNLOADED,
} from '../../constants/actionTypes'

const mapStateToProps = (state) => ({
  ...state.common.settings,
  ...state.courses,
})

const mapDispatchToProps = (dispatch) => ({
  onDrop: (users, courseId) =>
    dispatch({
      type: IMPORT_STUDENTS,
      payload: agent.Students.import(users, courseId),
      snackbar: { message: 'Students imported', variant: 'success' },
    }),
  onExit: () => dispatch({ type: STUDENT_DIALOG_UNLOADED }),
})

const ImportStudentsDialog = ({
  course,
  defaultStudentPassword,
  errors,
  inProgress,
  onDrop,
  onExit,
}) => {
  const [open, setOpen] = useState(false)

  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('md'))

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleFiledrop = (file) => {
    const column = Object.keys(file[0])
    const users = file
      .map((row, index) => {
        // Parse csv data for Infinite Campus grade book export
        if (index < 2) return null
        const student = row[column[0]]
        const user = {
          firstName: student
            .split(',')[1]
            .split(/(\s\w)*\s#/)[0]
            .trim(),
          lastName: student.split(',')[0],
          username: student.split('#')[1],
          password: defaultStudentPassword,
          role: 'student',
        }
        return user
      })
      .filter((element) => element !== null)
    onDrop(users, course._id)
  }

  useEffect(() => {
    // Close the dialog only if there are no errors
    if (!inProgress && !errors) setOpen(false)
  }, [inProgress])

  return (
    <>
      <ActionButton action='Import students' onClick={handleClickOpen}>
        <Publish />
      </ActionButton>

      <Dialog
        fullScreen={fullScreen}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onExit={onExit}
        onFocus={(ev) => ev.stopPropagation()}
        open={open}
      >
        <DialogTitle>Import Students</DialogTitle>
        <DialogContent>
          {inProgress ? (
            <Spinner message='Importing...' />
          ) : (
            <>
              <Typography gutterBottom>
                Students can be imported from a .csv file exported from Infinite
                Campus
              </Typography>
              <Filedrop onFiledrop={handleFiledrop} />
            </>
          )}
        </DialogContent>
        <DialogActions>
          <Button
            disabled={inProgress}
            onClick={handleClose}
            variant='contained'
          >
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </>
  )
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ImportStudentsDialog)
