import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import MaterialTable from 'material-table'
import { Grid, Paper, Typography, makeStyles } from '@material-ui/core'
import { Close, Edit } from '@material-ui/icons'

import ActionButton from '../components/ActionButton'
import ResetStudentPasswordAlert from './Teacher/ResetStudentPasswordAlert'
import TransactionDialog from './Teacher/TransactionDialog'
import agent from '../agent'
import convertScore from '../converter'

import {
  ACCOUNT_VIEW_LOADED,
  ACCOUNT_VIEW_UNLOADED,
  DELETE_TRANSACTION,
  UPDATE_TRANSACTION,
} from '../constants/actionTypes'

const useStyles = makeStyles((theme) => ({
  actions: {
    alignItems: 'flex-start',
    display: 'flex',
    justifyContent: 'flex-end',
  },
  balance: {
    'textAlign': 'center',
    '& > h6': {
      borderTop: '1px solid #ccc',
      fontStyle: 'italic',
      textTransform: 'lowercase',
    },
  },
  edit: {
    backgroundColor: theme.palette.primary.main,
  },
  paper: {
    padding: theme.spacing(2),
  },
  subtotal: {
    'position': 'relative',
    'textAlign': 'center',
    '& > p': {
      position: 'relative',
      bottom: -theme.spacing(1),
    },
    '& > h6': {
      fontSize: theme.typography.pxToRem(12),
      fontStyle: 'italic',
      textTransform: 'lowercase',
    },
  },
  unit: {
    fontSize: theme.typography.pxToRem(12),
  },
}))

const mapStateToProps = (state) => ({
  conversions: state.scores.conversions,
  total: state.scores.total,
  transactions: state.scores.transactions,
})

const mapDispatchToProps = (dispatch) => ({
  onDelete: (transaction) =>
    dispatch({
      type: DELETE_TRANSACTION,
      payload: agent.Transactions.delete(transaction),
      snackbar: { message: 'Transaction deleted', variant: 'success' },
    }),
  onLoad: (username) =>
    dispatch({
      type: ACCOUNT_VIEW_LOADED,
      payload: Promise.all([
        agent.Scores.getAll(username),
        agent.Products.getAll(),
      ]),
    }),
  onUnload: () => dispatch({ type: ACCOUNT_VIEW_UNLOADED }),
  onUpdate: (transaction) =>
    dispatch({
      type: UPDATE_TRANSACTION,
      payload: agent.Transactions.update(transaction),
      snackbar: { message: 'Transaction updated', variant: 'success' },
    }),
})

const AccountHeader = ({ canEdit, doEdit, onEditClick, student, total }) => {
  const classes = useStyles()

  return (
    <Paper className={classes.paper}>
      <Grid container spacing={2}>
        <Grid item xs={12} md={4}>
          <Typography gutterBottom variant='h5'>
            {student.firstName} {student.lastName}
          </Typography>
          {student.course && (
            <Typography color='textSecondary' variant='subtitle1'>
              {student.course} &middot; Period {student.period}
            </Typography>
          )}
        </Grid>
        <Grid item xs={12} md={4} container>
          <Grid item xs={12} className={classes.balance}>
            <Typography
              color={total?.balance >= 0 ? 'primary' : 'secondary'}
              variant='h4'
            >
              {total?.balance} <span className={classes.unit}>pts</span>
            </Typography>
            <Typography color='textSecondary' variant='subtitle2'>
              Balance
            </Typography>
          </Grid>
          <Grid item xs={4} className={classes.subtotal}>
            <Typography color='primary' variant='body1'>
              {total?.bonuses} <span className={classes.unit}>pts</span>
            </Typography>
            <Typography color='primary' variant='subtitle2'>
              Bonuses
            </Typography>
          </Grid>
          <Grid item xs={4} className={classes.subtotal}>
            <Typography color='secondary' variant='body1'>
              {total?.purchases} <span className={classes.unit}>pts</span>
            </Typography>
            <Typography color='secondary' variant='subtitle2'>
              Purchases
            </Typography>
          </Grid>
          <Grid item xs={4} className={classes.subtotal}>
            <Typography color='primary' variant='body1'>
              {total?.scores} <span className={classes.unit}>pts</span>
            </Typography>
            <Typography color='primary' variant='subtitle2'>
              Assessments
            </Typography>
          </Grid>
        </Grid>
        {canEdit && (
          <Grid item xs={12} md={4} className={classes.actions}>
            <TransactionDialog student={student} type='Bonus' />
            <TransactionDialog student={student} type='Purchase' />
            <ActionButton
              action={doEdit ? 'Finish editing' : 'Edit transactions'}
              className={doEdit && classes.edit}
              onClick={onEditClick}
            >
              {doEdit ? <Close /> : <Edit />}
            </ActionButton>
            <ResetStudentPasswordAlert btnSize='medium' student={student} />
          </Grid>
        )}
      </Grid>
    </Paper>
  )
}

const TransactionList = ({
  conversions,
  doEdit,
  onDelete,
  onUpdate,
  transactions,
}) => {
  const [data, setData] = useState([])

  const mapTransaction = (transaction) => ({
    amount: transaction.amount,
    createdAt: new Date(transaction.createdAt).toDateString(),
    description: transaction.description,
    type: transaction.type,
    _id: transaction._id,
  })

  const mapScore = (score) => ({
    amount: convertScore(score, conversions),
    createdAt: new Date(score.createdAt).toDateString(),
    description: score.task.title,
    type: 'assessment',
  })

  // @todo: sort by date?
  const mappedData = (transactions) => {
    const { bonuses, purchases, scores } = transactions
    const mapped = [
      ...bonuses.map((bonus) => mapTransaction(bonus)),
      ...purchases.map((purchase) => mapTransaction(purchase)),
    ]
    if (doEdit) {
      return mapped
    } else {
      return [...mapped, ...scores.map((score) => mapScore(score))]
    }
  }

  const [columns] = useState([
    {
      title: 'Date',
      field: 'createdAt',
      editable: 'never',
      defaultSort: 'desc',
    },
    { title: 'Description', field: 'description' },
    { title: 'Amount', field: 'amount' },
    { title: 'Type', field: 'type', editable: 'never' },
  ])

  useEffect(() => {
    transactions && setData(mappedData(transactions))
  }, [doEdit, transactions])

  return (
    <MaterialTable
      columns={columns}
      data={data}
      localization={{
        body: {
          emptyDataSourceMessage: 'No activity to report',
          editRow: {
            deleteText: 'Delete this transaction?',
          },
        },
      }}
      options={{ actionsColumnIndex: -1, paging: false, toolbar: false }}
      editable={
        doEdit
          ? {
              onRowUpdate: (newData, oldData) =>
                new Promise((resolve, reject) => {
                  const dataUpdate = [...data]
                  const index = oldData.tableData.id
                  dataUpdate[index] = newData
                  setData([...dataUpdate])
                  onUpdate(newData)
                  resolve()
                }),
              onRowDelete: (oldData) =>
                new Promise((resolve, reject) => {
                  const newData = [...data]
                  const index = oldData.tableData.id
                  newData.splice(index, 1)
                  setData([...newData])
                  onDelete(oldData)
                  resolve()
                }),
            }
          : {}
      }
    />
  )
}

const AccountView = ({
  canEdit,
  conversions,
  onDelete,
  onLoad,
  onUnload,
  onUpdate,
  student,
  total,
  transactions,
}) => {
  const [doEdit, setDoEdit] = useState(false)

  const handleEditClick = () => {
    setDoEdit(!doEdit)
  }

  useEffect(() => {
    return () => {
      onUnload()
    }
  }, [])

  useEffect(() => {
    onLoad(student.username)
  }, [student])

  return (
    <>
      {transactions && (
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <AccountHeader
              canEdit={canEdit}
              doEdit={doEdit}
              onEditClick={handleEditClick}
              student={student}
              total={total}
            />
          </Grid>
          <Grid item xs={12}>
            <TransactionList
              conversions={conversions}
              doEdit={doEdit}
              onDelete={onDelete}
              onUpdate={onUpdate}
              transactions={transactions}
            />
          </Grid>
        </Grid>
      )}
    </>
  )
}

export default connect(mapStateToProps, mapDispatchToProps)(AccountView)
